<template>
<div id="product-detail">
<van-nav-bar
  :title="detailInfo.title"
  left-arrow
  class="product-detail-nav"
   @click-left="leftClick"
   @click-right="rightClick"
>
<template #right>
	<i class="right-icon"><img src="~assets/images/common/icon/update.svg"></i>
</template>
</van-nav-bar>
	<div class="product-content">
		<!--轮播图-->
		<div class="swipe-box">
			<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
				<van-swipe-item v-for="item in swiperList" :key="item">
					<img v-lazy="item"/>
				</van-swipe-item>
			</van-swipe>
			<p class="product-title">{{detailInfo['title']}}</p>
		</div>
		<!--成本价-->
		<div class="price-box">
			<h5 class="box-title">成本分析(元)</h5>
			<div class="box-info">
				<div class="info-text">
					<p>产品最低价</p>
					<p class="box-num">{{detailInfo.price}}</p>
				</div>
				<div class="info-text">
					<p>产品成本价</p>
					<p class="box-num">{{detailInfo.costs}}</p>
				</div>
				<div class="info-text" style="border:none;">
					<p>产品最高价</p>
					<p class="box-num">{{detailInfo.newprice}}</p>
				</div>
			</div>
		</div>
		<!--库存-->
		<div class="stock-box">
			<h5 class="box-title">库存信息</h5>
			<div class="box-info">
				<div class="total-box">
					<p class="kucun">库存合计:<span>150</span></p>
					<p class="jine">金额合计:<span>200</span></p>
				</div>
				<div >	备注：<i @click="deleteClick">删除</i></div>
			</div>
		</div>
		<!--货品信息-->
		<div class="productinfo-box">
			<h5 class="box-title">货品信息</h5>
			<ul class="ulinfo">
				<li>货品编码<span>{{detailInfo.encoded}}</span></li>
				<li>货品类型<span>{{detailInfo.ctitle}}</span></li>
				<li>生成厂商<span>{{detailInfo.manufacturer}}</span></li>
				<li>产品规格<span>{{detailInfo.specs}}</span></li>
				<li>产品重量<span>{{detailInfo.weight}}(G)</span></li>
				<li>产品颜色<span>{{detailInfo.color}}</span></li>
			</ul>
		</div>
		<!--需求客户-->
		<div class="customer-box">
			<h5 class="box-title">货品信息</h5>
			<ul class="ulinfo">
				<li>货品编码<span>{{detailInfo.encoded}}</span></li>
				<li>货品类型<span>{{detailInfo.ctitle}}</span></li>
				<li>生成厂商<span>HP0001</span></li>
			</ul>
		</div>
		
	</div>
</div>
	
</template>

<script>
import { NavBar,Swipe, SwipeItem  } from 'vant'
import {getProductDetail,deleteProduct} from 'network/product'

export default {
  name: 'productdetail',
	components:{
		[NavBar.name]:NavBar,
		[Swipe.name]:Swipe,
		[SwipeItem.name]:SwipeItem,
	},
	data(){
		return{
			detailInfo:'',
			swiperList:[]
		}
	},
	methods:{	
		//这里需要获取详细商品的信息
		//返回上级
		leftClick(){
			this.$router.push('/product').catch(err=>{err})
		},
		//修改数据按钮
		rightClick(){
			//设置缓存
			localStorage.setItem('updateproductid',this.detailInfo.id)
			//跳转页面
			this.$router.push('/updateproduct').catch(err=>{err})
			return true
		},
		deleteClick(){
			let _self=this
			let id=this.detailInfo.id
			//先来个弹窗警告
			this.$dialog.confirm({
				title: '警告',
				message:
					'请注意该操作不可恢复,是否继续删除该条数据?',
				})
				.then(() => {
					//删除数据库里的(数据库有必要给一个删除成功与否的标识)
					//先删除数据库里面的在删除数组的
					deleteProduct(id).then(res=>{
						setTimeout(()=>{
							_self.$toast(res)
							this.$router.go(-1)
						},800)
					})
					// on confirm
					//这里确定了就删除指定数组的下标数据
				})
				.catch(() => {
					// on cancel
					_self.$toast('取消')
			});
			//这里删除完应该跳到列表页面的最后一页
		}
	},
	activated(){
		//获取数据
		//let id=this.$route.params.id?this.$route.params.id:1;
		let id=localStorage.getItem("productdetailid")?localStorage.getItem("productdetailid"):1;
		getProductDetail(id).then(res=>{
			this.detailInfo=res[0]
			this.swiperList=res[0]['swiper']
		})
	}
}
</script>

